/**
 * 页面loading图
 * @author wanglonghai
 * @date 20161129
 * 仅适用于public-wap.css的页面
 </script>
 */
define('conf/public/loading', function(require, exports, module) {

	class Loading{
		constructor(){
			this.isShow = false;
			this.loading = false;
			this._init();
		}
		_init(){
			if(this.loading){
				return false;
			}
			this._insertLoading();
		}
		_insertLoading(){
			this.loading = document.createElement('div');
			this.loading.className = 'cover';
			this.loading.style.top = '0';
			this.loading.style.left = '0';
			this.loading.style.right = '0';
			this.loading.style.bottom = '0';
			this.loading.style.width = '100%';
			this.loading.style.height = '100%';
			this.loading.style.display = 'none';
			this.loading.style.zIndex = 1000000;
			this.loading.style.position = 'fixed';
			this.loading.innerHTML = '<div class="m-toast m-toast-logo"><div class="toast-hd"><div class="toast-logo"></div></div><div class="toast-txt">正在加载...</div></div></div>';
			document.body.appendChild(this.loading);
		}
		addLoading(){
			if(this.isShow){
				return false;
			}
			this.loading.style.display = 'block';
			this.isShow = true;
		}
		removeLoading(){
			if(this.isShow){
				this.loading.style.display = 'none';
				this.isShow = false;
			}
		}
	}
	
	module.exports = new Loading();
});